.Toolbar {
    height: 3.5rem;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #044599;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
    box-sizing: border-box;
    z-index: 90;
    border-bottom: 2px solid black;
}

.Toolbar nav {
    height: 100%;
}

.Logo {
    height: 75%;
}

@media (max-width: 499px) {
    .DesktopOnly {
        display: none;
    }
}
